<template>
  <div>
    <div class="login-container container-fluid">
      <b-form>
        <b-form-group>
          <b-form-input
            v-model="email"
            type="email"
            required
            placeholder="Enter email"
          ></b-form-input>
        </b-form-group>
        <b-form-group>
          <b-form-input
            v-model="password"
            type="password"
            required
            placeholder="Enter password"
          ></b-form-input>
        </b-form-group>
        <b-button type="button" @click="signIn()" value="submit">Submit</b-button>
      </b-form>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Login',
  data () {
    return {
      email: "",
      password: "",
      isAdmin: false
    }
  },
  methods: {
    signIn() {
      let that = this;
      axios.post('api/users/login/', {
        email: this.email,
        password: this.password
      }).then(function(res) {
        let data = res.data;
        that.isAdmin = data.admin;
        localStorage.setItem('user',JSON.stringify(data.user))
        localStorage.setItem('jwt',data.token)

        if (localStorage.getItem('jwt') != null){
          that.$emit('loggedIn');
          if(that.$route.params.nextUrl != null){
            that.$router.push(that.$route.params.nextUrl);
          } else {
            that.$router.push('admin');
          }
        }
        return data;
      }).catch(function(err) {
        return err;
      });
    }
  }
}
</script>