<template>
  <div class="static-main-bg">
    <img v-if="!isSelectedBackgroundImage" src="../assets/images/yacht-sunset.jpg" alt="Yacht facing sunset" class="static-bg-img">
    <img v-if="isSelectedBackgroundImage" :src="`https://s3-us-west-1.amazonaws.com/oceans-yachting/${currentBackgroundImage.photo_name}`" :alt="`${currentBackgroundImage.alt}`" class="static-bg-img">
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'BackgroundStatic',
  data() {
    return {
      isSelectedBackgroundImage: false,
      currentBackgroundImage: ""
    }
  },
  methods: {
    getAllBackgrounds() {
      let that = this;
      axios.get("/api/backgrounds/").then(function(result) {
        let data = result.data.backgrounds
        that.backgrounds = data; 
        for(let i = 0; i < that.backgrounds.length; i++) {
          if(that.backgrounds[i].isCurrentBackground) {
            that.isSelectedBackgroundImage = true;
            that.currentBackgroundImage = that.backgrounds[i];
          }
        }
      }).catch(function(err) {
        if(err.response.status === 403) {
          that.logout();
        }
      });
    }
  },
  mounted () {
    this.getAllBackgrounds();
  }
}
</script>