<template>
  <div>
    <div class="section-padding">
      <a name="blog"></a>
      <div class="section-header-padding-top"></div>
      <h1 class="text-center section-header">Captain's Blog</h1>
      <div class="section-header-underline"></div>
      <div class="section-header-padding-bottom"></div>
      <div class="row section-content-container">
        <div class="col-md-12 col-sm-12 col-xs-12">
          <div class="text-container" v-for="item in post" v-bind:key="item._id">
            <div class="row">
              <div class="col-md-6">
                <h3>{{item.title}}</h3>
              </div>
              <div class="col-md-6">
                {{moment(item.createdAt)}}
              </div>
            </div>
            <p>{{item.body}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'

export default {
  name: 'Blog',
  data() {
    return {   
      posts: [],
      post: [],
      title: "",
      body: ""
    }
  },
  mounted () {
    var that = this;
    axios.get("/api/posts/").then(function(res) {
      let data = res.data.posts
      function compare(a, b) {
          if (a.createdAt > b.createdAt)
            return -1;
          if (a.createdAt < b.createdAt)
            return 1;
          return 0;
        }   
        that.posts = data.sort(compare); 
        that.post.push(that.posts[0]);
    }).catch(function(err) {
      console.log(err)
    })
  },
  methods: {
    moment(timestamp) {
      return moment(timestamp).format('MMMM Do YYYY, h:mm a');
    }
  }
}
</script>