import axios from 'axios'
/* eslint-disable */
const url = 'api/posts/'
let token = localStorage.getItem('jwt');
axios.defaults.headers.common = {'Authorization': `Bearer ${token}`}

class PostService {
  static getPosts () {
    axios.get(url).then(function(res) {
      let data = res.data.posts;
      resolve(data);
    }).catch(function(err) {
      return err;
    });
  }

  static addPost (title, body) {
    return axios.post(url, {
      title: title,
      body: body
    });
  }

  static updatePost (title, body, id) {
    axios.put(`${url}${id}`, {
      title: title,
      body: body
    }).then(function(data) {
      return data;
    }).catch(function(err) {
      return err;
    });
  }

  static deletePost (id) {
    return axios.delete(`${url}${id}`)
  }
}

export default PostService
