<template>
  <div>
		<b-navbar toggleable="lg" class="navbar-main">
      <div class="container-fluid navbar-padding">
        <b-navbar-brand class="navbar-brand" href="/"></b-navbar-brand>

        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
			
        <b-collapse id="nav-collapse" is-nav right>
					<b-navbar-nav class="nav navbar-items">
						<b-nav-item href="/admin">Admin Dashboard</b-nav-item>
            <b-nav-item href="/edit-blog">Blog</b-nav-item>
            <b-nav-item href="/edit-company-profile">Company Profile</b-nav-item>
					</b-navbar-nav>
					<b-navbar-nav class="nav navbar-items pull-right">
						<b-button type="button" @click="logout()">log out</b-button>
					</b-navbar-nav>
        </b-collapse>
      </div>
    </b-navbar>

    <div class="section-header-padding-top"></div>
    <div class="section-header-padding-bottom"></div>
    <div class="section-content-container">
      <h3 class="text-center">Select Background Image</h3>
      <div class="background-preview-wrapper">
        <div class="row">
          <div class="col-md-3" v-for="background in backgrounds.slice(indexStart, indexEnd)" v-bind:key="background._id">
            <img @click="toggleSelectedBackground(`${background._id}`)" :src="`https://s3-us-west-1.amazonaws.com/oceans-yachting/${background.photo_name}`" :alt="`${background.alt}`" v-bind:class="{ selectedBackground: isSelectedBackground(`${background._id}`) }" class="background-preview">
          </div>
        </div>
      </div>
      <div class="section-header-padding-bottom"></div>
      <div class="section-header-padding-bottom"></div>
      <div v-if="!singlePage" class="text-center pagination-group">
        <font-awesome-icon @click="prevPage()" :icon="{ prefix: 'fas', iconName: 'angle-left' }" class="prev-page"/>
        {{currentPage}}
        <font-awesome-icon @click="nextPage()" :icon="{ prefix: 'fas', iconName: 'angle-right' }" class="next-page"/>
      </div>
      <div class="section-header-padding-bottom"></div>
      <div class="row">
        <div class="col-md-6">
          <b-button @click="setNewBackground(selectedBackgroundImageId)" v-if="showSelectBackgroundSubmit">Save</b-button>
          <b-button @click="cancelSetNewBackground()" v-if="showSelectBackgroundSubmit" class="button-margin-left">Cancel</b-button>
        </div>
        <div class="col-md-6">
          <b-button v-b-modal.bg-image-modal>Add New Background Image</b-button>
        </div>
      </div>
    </div>

    <!-- Add new background modal -->
    <div>
      <b-modal ref="background-modal" id="bg-image-modal" hide-footer title="New Background">
        <b-form v-if="showNewBackgroundForm">
          Upload New Image
          <b-form-group>
            <b-form-file
              placeholder="Choose a file..."
              @change="onFileSelected"
              class="file-upload"
              ref="file-input"
            ></b-form-file>
          </b-form-group> 
          Alt Name (helps with screen readers and seo)
          <b-form-group>
            <b-form-input
              type="text"
              v-model="alt"
              placeholder="ex: Yacht facing sunset"
            ></b-form-input>
          </b-form-group> 
          <div class="text-center">     
            <b-button type="submit" @click.prevent="addBackgroundImage(selectedImage, alt)" variant="primary">Submit</b-button>
          </div>
        </b-form>
      </b-modal>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Admin',
  data () {
    return {
      backgrounds: [],
      currentBackgroundImage: "",
      selectedBackgroundImageId: "",
      selectedImage: "",
      alt: "", 
      isValidForm: false,
      message: "",
      error: "",
      showAlert: true,
      showAllBackgrounds: false,
      showNewBackgroundForm: true,
      showSelectBackgroundSubmit: false,
      numPages: 0,
      singlePage: false,
      currentPage: 1,
      indexStart: 0,
      indexEnd: 4
    }
	},
	methods: {
    checkAuth() {
      return new Promise(function(resolve, reject) {
        if(localStorage.getItem('jwt') != null && localStorage.getItem('user') != null) {
          resolve();
        } else {
          reject();
        }
      });
    },
    logout() {
			localStorage.clear();
			this.$router.go('/admin');
    },
    getAllBackgrounds() {
      let that = this;
      axios.get("/api/backgrounds/").then(function(result) {
        let data = result.data.backgrounds
        function compare(a, b) {
          if (a.isCurrentBackground < b.isCurrentBackground)
            return 1;
          if (a.isCurrentBackground > b.isCurrentBackground)
            return -1;
          return 0;
        }
        that.backgrounds = data.sort(compare); 
        if(that.backgrounds) {
          if(that.backgrounds.length % 4 === 0) {
            that.numPages = that.backgrounds.length / 4;
          } else {
            if(that.backgrounds.length < 4) {
              that.singlePage = true;
            }
            that.numPages = Math.floor((that.backgrounds.length / 4) + 1);
          }
          for(let i = 0; i < that.backgrounds.length; i++) {
            if(that.backgrounds[i].isCurrentBackground) {
              that.currentBackgroundImage = that.backgrounds[i];
              that.selectedBackgroundImageId = that.backgrounds[i]._id;
            }
          }
        }
      }).catch(function(err) {
        if(err.response.status === 403) {
          return err;
        }
      });
    },
    isSelectedBackground(id) {
      if(id === this.selectedBackgroundImageId) {
        return true;
      } else {
        return false;
      }
    },
    toggleSelectedBackground(id) {
      this.selectedBackgroundImageId = id;
      this.showSelectBackgroundSubmit = true;
    },
    showSelectBackgrounds() {
      this.showAllBackgrounds = true;
      this.showNewBackgroundForm = false;
    },
    onFileSelected(event) {
      this.selectedImage = event.target.files[0];
      const files = event.target.files;
      if (files && files[0]) {
        const reader = new FileReader;
        reader.onload = e => {
          this.imageData = e.target.result;
        }
        reader.readAsDataURL(files[0]);
      }
    },
    addBackgroundImage(photo, alt) {
      let that = this;
      let formData = new FormData();
      this.validateForm(photo);
      if(this.isValidForm) {
        formData.append("photo", photo);
        formData.append("alt", alt);
        formData.append("isCurrentBackground", true);

        axios.post("/api/backgrounds/", formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }).then(function(result) {
          that.$refs['file-input'].reset();
          that.selectedImage = "";
          that.alt = "";
          that.$refs['background-modal'].hide();      
          that.getAllBackgrounds();
          that.currentPage = 1;
          that.indexStart = 0;
          that.indexEnd = 4;
          return result;
        }).catch(function(err) {
          return err;
        });
      }
    },
    validateForm(image) {
      if(!image) {
        this.error = "Image is required"
        this.showAlert = true;
      } else {
        this.isValidForm = true;
      }
    },
    prevPage() {
      if(this.currentPage - 1 > 0) {
        this.currentPage -= 1;
        this.indexEnd = this.indexStart;
        this.indexStart = this.indexStart - 4;
      }
    },
    nextPage() {
      if(this.currentPage + 1 <= this.numPages) {
        this.currentPage += 1;
        this.indexStart = this.indexEnd;
        if(this.currentPage === this.numPages) {
          this.indexEnd = this.backgrounds.length;
        } else {
          this.indexEnd = this.indexEnd + 4;
        }
      }
    },
    setNewBackground(id) {
      let that = this;
      let formData = new FormData();
      formData.append("isCurrentBackground", true);
      formData.append("currentBackgroundId", this.currentBackgroundImage._id);
      axios.put("/api/backgrounds/" + id, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(function(result) {
        that.getAllBackgrounds();
        that.currentPage = 1;
        that.indexStart = 0;
        that.indexEnd = 4;
        that.showSelectBackgroundSubmit = false;
      }).catch(function(err) {
        return err;
      });
    },
    cancelSetNewBackground() {
      this.showSelectBackgroundSubmit = false;
      this.selectedBackgroundImageId = this.currentBackgroundImage._id;
    }
  },
  mounted() {
    let that = this;
    this.checkAuth().then(function(result) {
      that.getAllBackgrounds();
    }).catch(function(err) {
      that.logout();
    });
  }
}
</script>