<template>
  <div>
		<b-navbar toggleable="lg" class="navbar-main">
      <div class="container-fluid navbar-padding">
        <b-navbar-brand class="navbar-brand" href="/"></b-navbar-brand>

        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
			
        <b-collapse id="nav-collapse" is-nav right>
					<b-navbar-nav class="nav navbar-items">
						<b-nav-item href="/admin">Admin Dashboard</b-nav-item>
            <b-nav-item href="/edit-blog">Blog</b-nav-item>
            <b-nav-item href="/edit-company-profile">Company Profile</b-nav-item>
					</b-navbar-nav>
					<b-navbar-nav class="nav navbar-items pull-right">
						<b-button type="button" @click="logout()">log out</b-button>
					</b-navbar-nav>
        </b-collapse>
      </div>
    </b-navbar>
    <div class="section-header-padding-top"></div>
    <h1 class="text-center section-header">Company Profile</h1>
    <div class="section-header-underline"></div>
    <div class="section-header-padding-bottom"></div>
    <b-alert v-if="message && showAlert" class="text-center" show variant="success">
      {{message}}            
      <font-awesome-icon @click.prevent="hideAlert()" class="" :icon="{ prefix: 'fas', iconName: 'window-close' }" />
    </b-alert>
    <b-alert v-if="error && showAlert" class="text-center" show variant="danger">
      {{error}}
      <font-awesome-icon @click.prevent="hideAlert()" class="" :icon="{ prefix: 'fas', iconName: 'window-close' }" />
    </b-alert>
    <div class="section-header-padding-bottom"></div>
    <div v-if="!showEditForm" class="row section-content-container">
      <div class="col-md-12 col-sm-12 col-xs-12">
        <h3>New Employee</h3>
        <b-form>
          <b-form-group>
            <b-form-input
              type="text"
              v-model="firstName"
              placeholder="First Name"
            ></b-form-input>
          </b-form-group>  
          <b-form-group>
            <b-form-input
              type="text"
              v-model="lastName"
              placeholder="Last Name"
            ></b-form-input>
          </b-form-group>    
          <b-form-group>
            <b-form-input
              type="text"
              v-model="title"
              placeholder="Title"
            ></b-form-input>
          </b-form-group>          
          <b-form-group>
            <b-form-textarea
              v-model="bio"
              rows="3"
              placeholder="Bio"
            ></b-form-textarea>
          </b-form-group>   
          <b-form-group>
            <b-form-textarea
              v-model="quote"
              rows="3"
              placeholder="Quote"
            ></b-form-textarea>
          </b-form-group>
          Profile Photo
          <b-form-group>
            <b-form-file
              placeholder="Choose a file..."
              @change="onFileSelected"
              class="file-upload"
              ref="file-input"
            ></b-form-file>
          </b-form-group>   
          <b-form-group>
            <b-form-textarea
              v-model="bulletPoints"
              rows="3"
              placeholder="Vessel(s) *separate with comma"
            ></b-form-textarea>
          </b-form-group>   
          <b-button type="submit" @click.prevent="addEmployee()" variant="primary">Submit</b-button>
        </b-form>
      </div>
    </div>

    <div class="row section-content-container">
      <div class="col-md-12 col-sm-12 col-xs-12">
        <div class="blog-text-container" v-for="employee in employees" v-bind:key="employee._id">
          <div v-if="!showEditForm || employee._id !== selectedEmployeeId">
            <b>Name</b>
            <div class="row">
              <div class="col-md-12">
                <p>{{employee.first_name}} {{employee.last_name}}</p>
              </div>
            </div>
            <b>Title</b>
            <div class="row">
              <div class="col-md-12">
                <p>{{employee.title}}</p>
              </div>
            </div>
            <b>Bio</b>
            <div class="row">
              <div class="col-md-12">
                <p>{{employee.bio}}</p>
              </div>
            </div>
            <b>Quote</b>
            <div class="row">
              <div class="col-md-12">
                <p>{{employee.quote}}</p>
              </div>
            </div>
            <b>Profile Photo</b>
            <div class="row">
              <div class="col-md-12">
                <img v-if="employee.photo_name" class="profile-photo-thumb" :src="`https://s3-us-west-1.amazonaws.com/oceans-yachting/${employee.photo_name}`" />
                <img v-if="!employee.photo_name" class="profile-photo-thumb" src="../assets/images/generic-profile-picture.jpg" />
              </div>
            </div>
            <b>Vessel(s)</b>
            <div v-if="employee.bullet_points[0] !== ''" class="row">
              <div class="col-md-12" v-for="bullet in employee.bullet_points" v-bind:key="bullet._id">
                <ul>
                  <li>{{bullet}}</li>
                </ul>
              </div>
            </div>
          </div>
          <div class="form-padding-bottom" v-if="showEditForm && employee._id === selectedEmployeeId">
            <b-form>
              First Name
              <b-form-group>
                <b-form-input
                  type="text"
                  v-model="updatedFirstName"
                  placeholder="First Name"
                ></b-form-input>
              </b-form-group>  
              Last Name
              <b-form-group>
                <b-form-input
                  type="text"
                  v-model="updatedLastName"
                  placeholder="Last Name"
                ></b-form-input>
              </b-form-group>
              Title    
              <b-form-group>
                <b-form-input
                  type="text"
                  v-model="updatedTitle"
                  placeholder="Title"
                ></b-form-input>
              </b-form-group>
              Bio          
              <b-form-group>
                <b-form-textarea
                  v-model="updatedBio"
                  rows="3"
                  placeholder="Bio"
                ></b-form-textarea>
              </b-form-group>   
              Quote
              <b-form-group>
                <b-form-textarea
                  v-model="updatedQuote"
                  rows="3"
                  placeholder="Quote"
                ></b-form-textarea>
              </b-form-group>   
              Profile Photo    
              <b-form-group>
                <b-form-file
                  placeholder="Choose a file..."
                  @change="onFileSelected"
                  class="file-upload"
                ></b-form-file>
              </b-form-group>   
              Vessel(s) *separate with comma
              <b-form-group>
                <b-form-textarea
                  v-model="updatedBulletPoints"
                  rows="3"
                  placeholder="Vessel(s) *separate with comma"
                ></b-form-textarea>
              </b-form-group>   
              <b-form>
              <b-button type="submit" @click.prevent="updateEmployee(updatedFirstName, updatedLastName, updatedTitle, updatedBio, updatedQuote, selectedImage, updatedBulletPoints, `${employee._id}`)" variant="primary">Submit</b-button>
              <b-button @click.prevent="cancel()">Cancel</b-button>
            </b-form>
            </b-form>
          </div>
          <div class="blog-actions">
            <font-awesome-icon @click.prevent="editEmployee(`${employee._id}`)" class="blog-padding-edit" :icon="{ prefix: 'fas', iconName: 'edit' }" />
            <font-awesome-icon @click.prevent="deleteEmployee(`${employee._id}`)" class="blog-delete" :icon="{ prefix: 'fas', iconName: 'minus-circle' }" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import EmployeeService from '../services/EmployeeService';

export default {
  name: 'EditCompanyProfile',
  data () {
    return {
      employees: [],
      firstName: "",
      lastName: "",
      title: "",
      bio: "",
      quote: "",
      selectedImage: "",
      bulletPoints: "",
      showEditForm: false,
      updatedFirstName: "",
      updatedLastName: "",
      updatedTitle: "",
      updatedBio: "",
      updatedQuote: "",
      updatedPhotoFile: "",
      updatedBulletPoints: "",
      selectedEmployeeId: "",
      isValidForm: false,
      message: "",
      error: "",
      showAlert: true
    }
	},
	methods: {
    logout() {
			localStorage.clear();
			this.$router.go('/admin');
    },
    hideAlert() {
      this.showAlert = false;
      this.message = "";
      this.error = "";
    },
    getEmployees() {
      let that = this;
      axios.get("/api/employees/admin/").then(function(res) {
        let data = res.data.employees
        function compare(a, b) {
          if (a.createdAt > b.createdAt)
            return 1;
          if (a.createdAt < b.createdAt)
            return -1;
          return 0;
        }   
        that.employees = data.sort(compare); 
      }).catch(function(err) {
        if(err.response.status === 403) {
          that.logout();
        }
      });
    },
    onFileSelected(event) {
      this.selectedImage = event.target.files[0];
      const files = event.target.files;
      if (files && files[0]) {
        const reader = new FileReader;
        reader.onload = e => {
          this.imageData = e.target.result;
        }
        reader.readAsDataURL(files[0]);
      }
    },
    addEmployee() {
      let that = this;
      this.validateForm(this.firstName, this.lastName);
      if(this.isValidForm) {
        EmployeeService.addEmployee(this.firstName, this.lastName, this.title, this.bio, this.quote, this.selectedImage, this.bulletPoints).then(function(result) {
          that.getEmployees();
          that.$refs['file-input'].reset()      
          that.firstName = "";
          that.lastName = "";
          that.title = "";
          that.bio = "";
          that.quote = "";
          that.selectedImage = "";
          that.bulletPoints = "";
          if(result.status === 200) {
            that.message = result.data.message;
            that.showAlert = true;
          } 
        }).catch(function(err) {
          return err;
        });
      }
    },
    editEmployee(id) {
      let that = this;
      this.showEditForm = true;
      axios.get("/api/employees/" + id).then(function(res) {
        let data = res.data.employee;
        that.updatedFirstName = data.first_name;
        that.updatedLastName = data.last_name;
        that.updatedTitle = data.title;
        that.updatedBio = data.bio;
        that.updatedQuote = data.quote;
        that.updatedBulletPoints = data.bullet_points.toString();
        that.selectedEmployeeId = data._id;
        return data;
      }).catch(function(err) {
        return err;
      });
    },
    updateEmployee(firstName, lastName, title, bio, quote, photo, bulletPoints, id) {
      let that = this;
      let formData = new FormData();
      this.validateForm(firstName, lastName);
      if(this.isValidForm) {
        formData.append("first_name", firstName);
        formData.append("last_name", lastName);
        formData.append("title", title);
        formData.append("bio", bio);
        formData.append("quote", quote);
        formData.append("photo", photo);
        formData.append("bullets", bulletPoints);

        axios.put("/api/employees/" + id, formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }).then(function(result) {
          if(result.status === 200) {
            that.message = result.data.message;
            that.showAlert = true;
          } 
          that.getEmployees();
          that.cancel();
          return result;
        }).catch(function(err) {
          return err;
        });
      }
    },
    validateForm(firstName, lastName) {
      if(!firstName) {
        this.error = "First Name is required"
        this.showAlert = true;
      } else if(!lastName) {
        this.error = "Last Name is required"
        this.showAlert = true;
      } else {
        this.isValidForm = true;
      }
    },
    cancel() {
      this.showEditForm = false;
    },
    deleteEmployee(id) {
      let that = this;
      EmployeeService.deleteEmployee(id).then(function(result) {
        if(result.status === 200) {
          that.message = result.data.message;
          that.showAlert = true;
        } 
        that.getEmployees();
      });
    }
  },
  mounted () {
    this.getEmployees();
  }
}
</script>