<template>
  <div>
		<b-navbar toggleable="lg" class="navbar-main">
      <div class="container-fluid navbar-padding">
        <b-navbar-brand class="navbar-brand" href="/"></b-navbar-brand>

        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
			
        <b-collapse id="nav-collapse" is-nav right>
					<b-navbar-nav class="nav navbar-items">
            <b-nav-item href="/admin">Admin Dashboard</b-nav-item>
            <b-nav-item href="/edit-blog">Blog</b-nav-item>
            <b-nav-item href="/edit-company-profile">Company Profile</b-nav-item>
					</b-navbar-nav>
					<b-navbar-nav class="nav navbar-items pull-right">
						<b-button type="button" @click="logout()">log out</b-button>
					</b-navbar-nav>
        </b-collapse>
      </div>
    </b-navbar>
    <div class="section-header-padding-top"></div>
    <h1 class="text-center section-header">Captain's Blog</h1>
    <div class="section-header-underline"></div>
    <div class="section-header-padding-bottom"></div>
    <b-alert v-if="message && showAlert" class="text-center" show variant="success">
      {{message}}            
      <font-awesome-icon @click.prevent="hideAlert()" class="" :icon="{ prefix: 'fas', iconName: 'window-close' }" />
    </b-alert>
    <b-alert v-if="error && showAlert" class="text-center" show variant="danger">
      {{error}}
      <font-awesome-icon @click.prevent="hideAlert()" class="" :icon="{ prefix: 'fas', iconName: 'window-close' }" />
    </b-alert>
    <div class="section-header-padding-bottom"></div>
    <div v-if="!showEditForm" class="row section-content-container">
      <div class="col-md-12 col-sm-12 col-xs-12">
        <h3>New Post</h3>
        <b-form>
          <b-form-group>
            <b-form-input
              type="text"
              v-model="title"
              placeholder="New Blog Title"
            ></b-form-input>
          </b-form-group>        
          <b-form-group>
            <b-form-textarea
              v-model="body"
              rows="3"
              placeholder="New Blog Body"
            ></b-form-textarea>
          </b-form-group>   
          <b-button type="submit" @click.prevent="addPost()" variant="primary">Submit</b-button>
        </b-form>
      </div>
    </div>

    <div class="row section-content-container">
      <div class="col-md-12 col-sm-12 col-xs-12">
        <div class="blog-text-container" v-for="post in posts" v-bind:key="post._id">
          <div v-if="!showEditForm || post._id !== selectedPostId">
            <div class="row">
              <div class="col-md-6">
                <h3>{{post.title}}</h3>
              </div>
              <div class="col-md-6">
                {{moment(post.createdAt)}}
              </div>
            </div>
            <p>{{post.body}}</p>
          </div>
          <div class="form-padding-bottom" v-if="showEditForm && post._id === selectedPostId">
            {{moment(post.createdAt)}}
            <b-form>
              <b-form-group>
                <b-form-input
                  type="text"
                  v-model="updatedTitle"
                ></b-form-input>
              </b-form-group>        
              <b-form-group>
                <b-form-textarea
                  v-model="updatedBody"
                  rows="3"
                ></b-form-textarea>
              </b-form-group>   
              <b-button type="submit" @click.prevent="updatePost(updatedTitle, updatedBody, `${post._id}`)" variant="primary">Submit</b-button>
              <b-button @click.prevent="cancel()">Cancel</b-button>
            </b-form>
          </div>
          <div class="blog-actions">
            <font-awesome-icon @click.prevent="editPost(`${post._id}`)" class="blog-padding-edit" :icon="{ prefix: 'fas', iconName: 'edit' }" />
            <font-awesome-icon @click.prevent="deletePost(`${post._id}`)" class="blog-delete" :icon="{ prefix: 'fas', iconName: 'minus-circle' }" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from '../services/PostService'
import axios from 'axios'
import moment from 'moment'

export default {
  name: 'EditBlog',
  data () {
    return {
      posts: [],
      title: "",
      body: "",
      showEditForm: false,
      updatedTitle: "",
      updatedBody: "",
      selectedPostId: "",
      isValidForm: false,
      message: "",
      error: "",
      showAlert: true
    }
	},
	methods: {
    logout() {
			localStorage.clear();
			this.$router.go('/admin');
    },
    hideAlert() {
      this.showAlert = false;
      this.message = "";
      this.error = "";
    },
    getPosts() {
      let that = this;
      axios.get("/api/posts/admin/").then(function(res) {
        let data = res.data.posts
        function compare(a, b) {
          if (a.createdAt > b.createdAt)
            return -1;
          if (a.createdAt < b.createdAt)
            return 1;
          return 0;
        }   
        that.posts = data.sort(compare); 
      }).catch(function(err) {
        if(err.response.status === 403) {
          that.logout();
        }
      });
    },
    addPost() {
      let that = this;
      this.validateForm(this.body);
      if(this.isValidForm) {
        PostService.addPost(this.title, this.body).then(function(result) {
          that.getPosts();
          that.title = "";
          that.body = "";
          if(result.status === 200) {
            that.message = result.data.message;
            that.showAlert = true;
          } 
        }).catch(function(err) {
          return err;
        });
      }
    },
    editPost(id) {
      let that = this;
      this.showEditForm = true;
      axios.get("/api/posts/" + id).then(function(res) {
        let data = res.data.post;
        that.updatedTitle = data.title;
        that.updatedBody = data.body;
        that.selectedPostId = data._id;
        return data;
      }).catch(function(err) {
        return err;
      });
    },
    validateForm(body) {
      if(!body) {
        this.error = "Body is required"
        this.showAlert = true;
      } else {
        this.isValidForm = true;
      }
    },
    cancel() {
      this.showEditForm = false;
      this.isValidForm = false;
    },
    updatePost(title, body, id) {
      let that = this;
      this.validateForm(body);
      if(this.isValidForm) {
        axios.put("/api/posts/" + id, {
          title: title,
          body: body
        }).then(function(result) {
          if(result.status === 200) {
            that.message = result.data.message;
            that.showAlert = true;
          } 
          that.getPosts();
          that.cancel();
          return result;
        }).catch(function(err) {
          return err;
        });
      }
    },
    deletePost(id) {
      let that = this;
      PostService.deletePost(id).then(function(result) {
        if(result.status === 200) {
          that.message = result.data.message;
          that.showAlert = true;
        } 
        that.getPosts();
      });
    },
    moment(timestamp) {
      return moment(timestamp).format('MMMM Do YYYY, h:mm a');
    }
  },
  mounted () {
    this.getPosts();
  }
}
</script>